.header {
  grid-area: head;
  background-color: white;
  display: flex;
  align-items: center;
  padding: 0 15px;
  justify-content: space-between;
}
.header_container {
  padding-top: 5px;
  padding-bottom: 5px;
  display: flex;
  gap: 131px;
  align-items: center;
  min-height: 100px;
  justify-content: space-between;
}

.logo {
  width: 80px;
  cursor: pointer;
}